        left: calc(100% + 16px);
        left: calc(100% + var(--gutter));
        width: 350px; } }

.header__signup.is-visible {
      display: block;
      pointer-events: auto; }

.header__signup label,
    .header__signup input,
    .header__signup button {
      background: transparent;
      border: 0;
      display: block;
      font: normal 12px / 1.21 "SctoGroteskB";
      padding: 0;
      text-transform: uppercase; }

.header__signup button {
      cursor: pointer; }

.header__signup input[type="email"] {
