      width: 100%; }

.header__signup input ::-moz-placeholder {
      color: #afafaf;
      color: var(--grey);
      opacity: 1; }

.header__signup input ::placeholder,
    .header__signup input :-moz-placeholder,
    .header__signup input ::-moz-placeholder,
    .header__signup input :-ms-input-placeholder,
    .header__signup input ::-webkit-input-placeholder {
      color: #afafaf;
      color: var(--grey);
      opacity: 1; }

.animate {
  display: -webkit-inline-box;
  display: inline-flex;
  overflow: hidden;
  position: relative; }

.animate::before {
    -webkit-animation: flitter 3s ease infinite;
            animation: flitter 3s ease infinite;
    background: white;
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    top: 0;
    width: 7.5px; }

@-webkit-keyframes flitter {
  from {
    left: 0; }
  to {
    left: 300%; } }

@keyframes flitter {
  from {
    left: 0; }
  to {
    left: 300%; } }

.footer {
