        grid-template: "logo info talent link" auto / -webkit-max-content -webkit-max-content 1fr -webkit-max-content;
        grid-template: "logo info talent link" auto / max-content max-content 1fr max-content;
        padding: 16px;
        padding: var(--gutter); } }

.event__logo {
