      border: 0;
      cursor: pointer;
      display: block;
      font: normal 12px / 1.21 "SctoGroteskB";
      margin: 5px 0 0;
      opacity: 0;
      padding: 0;
      text-transform: uppercase; }

@media (min-width: 768px) {
        .event__link button {
          border: 1px solid;
          border-radius: 0;
